<template>
    <div>
        <StatusLayout ref="statuslayout"/>
    </div>
</template>

<script>
import StatusLayout from '../components/StatusLayout.vue'
export default {
    name: 'Note',
    components:{
        StatusLayout
    },
    mounted(){
        this.$refs.statuslayout.loading = true
        setTimeout(() => {
            this.$refs.statuslayout.loading = false
        },3000)
    }
}
</script>